/**
 * Retourner une date dans le format franais suivant : 
 *  5 novembre 2021
 * @param {Number} tsSecondes timestamp en secondes
 * @returns {String} Chane reprsentant le timestamp dans le format spcifi
 */

 export function formaterDate(tsSecondes) {
    let dateJS = new Date(tsSecondes*1000);
    let nomsDesMois = ['janvier', 'fvrier', 'mars', 'avril', 'mai', 'juin', 'juillet', 'aot', 'septembre', 'octobre', 'novembre', 'dcembre'];
    return `${dateJS.getDate()} ${nomsDesMois[dateJS.getMonth()]} ${dateJS.getFullYear()}`
}